import Cookie from 'universal-cookie';
import jwt from 'jsonwebtoken';
import { authvalidation } from '../actions/pns'
const cookie = new Cookie();

export const secretJWT = 'pusdatinforindonesia';
const validatejwt = ()=> {
  let _tokendata = {
    pns: {},
    allowedApplication: []
  }
  if (cookie.get('kemenkumhamssoengine') != null) {
    jwt.verify(cookie.get('kemenkumhamssoengine'), secretJWT, (err, tokendata)=> {
      if (!err) {
        _tokendata = {
          pns: {
            nip: tokendata.pns.nip,
            name: tokendata.pns.name,
            username: tokendata.pns.username
          },
          allowedApplication: tokendata.allowedApplication
        }
        return _tokendata;
      }
    });
  } else {
    return _tokendata;
  }
}

export const token = validatejwt();

export default {
  isAuthenticated: (localStorage.getItem('isAuthenticated') != null && cookie.get('kemenkumhamssoengine') != null),
  tokendata: cookie.get('kemenkumhamssoengine') != null ? jwt.decode(cookie.get('kemenkumhamssoengine'), secretJWT) : {pns: {}, allowedApplication: []},
  apiProcess: {
    isLoading: {
      form: {
        status: false,
        message: '',
        key: ''
      }, app: {
        status: false,
        message: ''
      }
    },
    hasError: {
      form: {
        status: false,
        message: '',
        key: ''
      }
    },
    isSuccess: {
      status: false,
      message: '',
      key: ''
    }
  }
}



